// in src/posts.js
import React from "react";
import {
  List,
  Edit,
  Datagrid,
  ReferenceField,
  TextField,
  EditButton,
  DisabledInput,
  LongTextInput,
  ReferenceInput,
  SelectInput,
  SimpleForm,
  TextInput,
  Filter,
  SimpleList,
  Responsive
} from "react-admin";
const PostFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput label="nama" source="nama" reference="myAccount" allowEmpty>
      <SelectInput optionText="name" />
    </ReferenceInput>
  </Filter>
);

export const PostList = props => (
  <List {...props} filters={<PostFilter />}>
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.title}
          secondaryText={record => `${record.views} views`}
          tertiaryText={record =>
            new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <ReferenceField
            label="Nama"
            source="myAccountId"
            reference="myAccount"
          >
            <TextField source="nama" />
          </ReferenceField>
          {/* <TextField source="nama" /> */}
          <TextField source="handphone" />
          <TextField source="alamat" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);
const PostTitle = ({ record }) => {
  return <span>Post {record ? `"${record.title}"` : ""}</span>;
};

export const PostEdit = props => (
  <Edit title={<PostTitle />} {...props}>
    <SimpleForm>
      <DisabledInput source="id" />
      <ReferenceInput label="nama" source="nama" reference="myAccount">
        <SelectInput optionText="name" />
      </ReferenceInput>
      <DisabledInput source="nama" />
      <TextInput source="handphone" />
      <LongTextInput source="alamat" />
    </SimpleForm>
  </Edit>
);
