// in src/myAccount.js
import React from "react";
import {
  List,
  Datagrid,
  TextField,
  Create,
  SimpleForm,
  TextInput
} from "react-admin";

export const myAccountList = props => (
  <List {...props}>
    <Datagrid>
      <TextField source="id" />
      <TextField source="nama" />
      <TextField source="handphone" />
      <TextField source="alamat" />
      <TextField source="rekening" />
    </Datagrid>
  </List>
);
export const myAccountCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <TextInput source="nama" />
      <TextInput source="handphone" />
      <TextInput source="alamat" />
      <TextInput source="rekening" />
    </SimpleForm>
  </Create>
);
